import Swiper, { Pagination, Navigation } from 'swiper';

export default () => {
  let windowWidth = window.innerWidth;
  let swiper;
  let inits = false;

  $(window).on('resize load', function () {
    windowWidth = window.innerWidth;
    if (windowWidth < 1024) {
      if (!inits) {
        inits = true;
        Swiper.use([Pagination, Navigation]);
        swiper = new Swiper('.articles .swiper-container', {
          slidesPerView: 'auto',
          centeredSlides: true,
          speed: 400,
          spaceBetween: 32,
          navigation: {
            prevEl: '.swiper-button-prev',
            nextEl: '.swiper-button-next',
          },
          pagination: {
            el: '.swiper-pagination',
            type: 'bullets',
            clickable: true,
          },
        });
      }
    } else {
      if (inits) {
        inits = false;
        swiper.destroy();
      }
    }
  });
};
