import { modalOpen, modalCloseFunc } from '@layouts/modal/modal';
export default () => {
  let districtName = $('.header-top__district');

  districtName.on('click', function () {
    modalOpen(this);
  });

  $('.modal-district__list button').on('click', function () {
    let data = $(this).data('district');
    districtName.find('span').text(data);
    modalCloseFunc();
  });
};
