import Swiper, { Pagination, Navigation } from 'swiper';

export default () => {
  Swiper.use([Pagination, Navigation]);
  let swiper = new Swiper('.types .swiper-container', {
    slidesPerView: 1,
    speed: 400,
    spaceBetween: 32,
    // centeredSlides: true,
    // loop: true,
    navigation: {
      prevEl: '.swiper-button-prev',
      nextEl: '.swiper-button-next',
    },
    pagination: {
      el: '.swiper-pagination',
      type: 'bullets',
      clickable: true,
    },
    breakpoints: {
      768: {
        slidesPerView: 2,
        spaceBetween: 32,
      },
      1024: {
        slidesPerView: 3,
        spaceBetween: 32,
      },
    },
  });
};
